<template>
  <div class="update-gender">
    <van-picker
      title=""
      show-toolbar
      :default-index="value"
      :columns="columns"
      @confirm="onConfirm"
      @cancel="$emit('close')"
      @change="onPickChange"
    />
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'

export default {
  name: 'UpdateGender',
  components: {},
  props: {
    value: {
      type: Number,
      required: true,
    }
  },
  data () {
    return {
      columns: ['', ''],
      localGender: this.value,
    }
  },
  watch: {},
  computed: {},
  methods: {
    async onConfirm() {
      this.$toast.loading({
        message: '...',
        forbidClick: true, // 
        duration: 0, // 
      })
      try {
        const loaclGender = this.loaclGender;
        await updateUserProfile({
          gender: loaclGender
        })
        this.$emit('input', loaclGender);

        this.$emit('close');

        this.$toast.success('');
      } catch (err) {
        this.$toast.fail('');
      }
    },

    onPickChange(picker, value, index) {
      this.loaclGender = index;
    }
  },
  created () {},
  mounted () {},
}
</script>

<style lang="less" scoped>
</style>
