<template>
  <div class="update-name">
    <van-nav-bar
      title=""
      left-text=""
      right-text=""
      @click-left="$emit('close')"
      @click-right="onConfirm"
    />
    <div class="filed-wrap">
      <van-field
      v-model="localName"
      rows="2"
      autosize
      type="textarea"
      maxlength="7"
      placeholder=""
      show-word-limit
    />
    </div>
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'

export default {
  name: 'UpdateName',
  components: {},
  props: {
    value: {
      type: String,
      required: true,
    }
  },
  data () {
    return {
      localName: this.value,
    }
  },
  watch: {},
  computed: {},
  methods: {
    async onConfirm() {
      this.$toast.loading({
        message: '...',
        forbidClick: true, // 
        duration: 0, // 
      })
      try {
        const localName = this.localName;
        if (!localName.length) {
          this.$toast('');
          return
        }
        await updateUserProfile({
          name: localName
        })
        this.$emit('input', localName);

        this.$emit('close');

        this.$toast.success('');
      } catch (err) {
        this.$toast.fail('');
      }
    }
  },
  created () {},
  mounted () {},
}
</script>

<style lang="less" scoped>
.filed-wrap {
  padding: 20px;
}
</style>
